<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div>
    <table>
      <thead>
        <tr>
          <th>任务</th>
          <th>开始时间</th>
          <th>截止时间</th>
          <th>isCompleted</th>
          <th>rest time</th>
          <th>操作</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in backendData" :key="item.id">
          <td>{{item.taskName}}</td>
          <td>{{item.startTime}}</td>
          <td contenteditable="true" @blur="updateEndTime($event, item)">{{ item.endTime }}</td>
          <td contenteditable="true" @blur="updatebool($event, item)">{{ item.isCompleted }}</td>
          <td>{{ item.timefield }}</td>
          <td>
            <button @click="editItem(item)">修改</button>
            <button @click="deleteItem(item)">删除</button>
          </td>
        </tr>
        <tr v-if="isNewRowVisible">
            <!-- 新增的行 -->
            <td>
                <div
                  contenteditable="true"
                  @input="updateNewtaskName($event)"
                >{{ newItem.taskName }}</div>
              </td>
            <td>
                ---
            </td>
            
            <td>
              <div
                contenteditable="true"
                @input="updateNewEndTime($event)"
              >{{newItem.endTime}}</div>
            </td>
            <td>---</td>
            <td>---</td>
            <td>
              <button @click="addItem">添加</button>
            </td>
          </tr>
      </tbody>

    </table>
    <button @click="showNewRow">新增任务</button>
  </div>
</template>
<style>
table {
  width: 80%;
  /* 调整表格宽度，根据需要调整 */
  margin: auto;
  /* 居中显示 */
  border-collapse: collapse;
  /* 合并单元格边框 */
  font-size: 16px;
  /* 调整字体大小，根据需要调整 */
}

th,
td {
  border: 1px solid #ddd;
  /* 添加边框 */
  padding: 8px;
  /* 调整单元格内边距，根据需要调整 */
  text-align: center;
  /* 文字居中 */
}

th {
  background-color: #f2f2f2;
  /* 添加表头背景色，根据需要调整 */
}

button {
  font-size: 14px;
  /* 调整按钮字体大小，根据需要调整 */
  padding: 5px 10px;
  /* 调整按钮内边距，根据需要调整 */
}</style>
<script>
import axios from 'axios';

export default {
  data() {
    return {
      backendData: [],
       newItem: {
        taskName: '',
        endTime: '2000-01-28T07:00:00',
      },
      isNewRowVisible: false, // 用于存储后端传来的数据
    };
  },
  mounted() {
    this.fetchData();
    setInterval(() => {
  this.refreshTimeDifference();
}, 100); 
  },
  
  // 每秒刷新一次
  methods: {
    updateEndTime(event, item) {
      // 更新项目的 endTime
      item.endTime = event.target.innerText;
    },  
    updatebool(event, item) {
      // 更新项目的 endTime
      item.isCompleted = event.target.innerText;
    },
     fetchData() {
      axios.get('/tasks/all')
        .then(response => {
          this.backendData = response.data;
        })
        .catch(error => {
          console.error('Error fetching data:', error);
        });
    },
    editItem(item) {
      // 在这里执行修改操作，例如弹出模态框等
      console.log('编辑项目:', item);
      // 这里可以添加逻辑以更新后端数据
      axios.post('/tasks/update', item)
        .then(response => {
          console.log('项目更新成功:', response.data);
        })
        .catch(error => {
          console.error('更新项目失败:', error);
        });
    },
    deleteItem(item) {
      // 在这里执行删除操作
      console.log('删除项目 ID:', item);
      // 这里可以添加逻辑以删除后端数据
      axios.post('/tasks/delete', item)
        .then(response => {
          console.log('项目删除成功:', response.data);
          // 在成功删除项目后刷新数据
          this.fetchData();
        })
        .catch(error => {
          console.error('删除项目失败:', error);
        });
    },
    addItem() {
      // 将新项目添加到后端数据中
      axios.post('/tasks/save', this.newItem)
        .then(response => {
          console.log('项目添加成功:', response.data);
          this.fetchData(); // 刷新数据
          this.isNewRowVisible = false; // 隐藏新增行
          // 重置新项目数据
          this.newItem = {
            taskName: '',
            endTime: '2000-01-28T07:00:00',
          };
        })
        .catch(error => {
          console.error('Error adding item:', error);
        });
    },
    showNewRow() {
      this.isNewRowVisible = true; // 显示新增行
    },
    updateNewEndTime(event) {
      // Update the endTime property of the newItem
      this.newItem.endTime = event.target.innerText;
    },
    updateNewtaskName(event) {
      // Update the endTime property of the newItem
      this.newItem.taskName = event.target.innerText;
    },
    calculateTimeDifference(item) {
      // 获取当前时间
      const now = new Date();
      const targetDate=new Date(item.endTime)
      // 计算时间间隔（单位：毫秒）
      const timeDiff = targetDate - now;

      // 将时间间隔转换为天数
      
      const days = Math.floor(timeDiff / (1000 * 60 * 60 * 24));

      // 计算剩余的小时数
      const hours = Math.floor((timeDiff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));

      // 计算剩余的分钟数
      const minutes = Math.floor((timeDiff % (1000 * 60 * 60)) / (1000 * 60));

      // 计算剩余的秒数
      const seconds = Math.floor((timeDiff % (1000 * 60)) / 1000);
      if (item.isCompleted)
        return 'finished'
      // 更新时间间隔的状态
      if (timeDiff > 0)
        return days + ' 天 '+hours+' 时 '+minutes+' 分 ' + seconds +' 秒 ';
      else
        return 'overtime';
    },
     refreshTimeDifference() {
      // 遍历每行数据，计算并更新时间间隔
      this.backendData.forEach(item => {
        item.timefield=this.calculateTimeDifference(item);
      });
    }
  },
  
  
}
</script>
